import * as THREE from 'three';
import { loadModels, updateAnimation } from './mesh/models.js';

//  
const scene = new THREE.Scene();

scene.fog = new THREE.Fog(0x363d3d, -1, 3000);
scene.fog.near = 0; //  0     
scene.fog.far = 1000; //  1000     


// Initialize the lights.
const ambientLight = new THREE.AmbientLight(0xffffff, 0.5);
const spotLight = new THREE.SpotLight(0xffffff, 0.8);
spotLight.position.set(200, 200, 0);
spotLight.castShadow = true;
scene.add(ambientLight);
scene.add(spotLight);

const hemisphereLight = new THREE.HemisphereLight(0xffffff, 0x000000, 0.5); //   
scene.add(hemisphereLight);



//  
const camera = new THREE.PerspectiveCamera(
  80,
  window.innerWidth / window.innerHeight,
  0.1,
  2000
);

camera.position.set(2, 4, 4.5);
// camera.position.x = 0; //  
// camera.position.y = 6; //  
// camera.position.z = 8; //  

//  
const renderer = new THREE.WebGLRenderer();
renderer.setSize(window.innerWidth, window.innerHeight);
document.body.appendChild(renderer.domElement);

//    
window.addEventListener('resize', function () {
  const width = window.innerWidth;
  const height = window.innerHeight;
  renderer.setSize(width, height);
  camera.aspect = width / height;
  camera.updateProjectionMatrix();
});

//  
loadModels(scene);

//    Clock
const clock = new THREE.Clock();

//    
function animate() {
    requestAnimationFrame(animate);
    const delta = clock.getDelta();
    updateAnimation(delta);
    renderer.render(scene, camera);
}

animate();




